@use 'variables' as *;

@mixin IntermediateView {
    @media (max-width:$intermediateView) {
        @content;
    }
}

@mixin TabletView {
    @media (max-width:$tabletView) {
        @content;
    }
}
@mixin MobileView {
    @media (max-width:$mobileView) {
        @content;
    }
}